(function() {
  'use strict';

  angular
    .module('app')
    .directive('erGrid', erGrid);

  /** @ngInject */
  function erGrid() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/grid/erGrid.tpl.html',
      scope: {},
      controller: ErGridController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function ErGridController($log, $timeout, GeoDataService, GridService, ItemDetailsService) {
      var vm = this;

      vm.gridOptions = GridService.gridOptions;

      GeoDataService.list.subscribe(function(list) {
        $log.log('Observable subscriber called ---> GRID ', list.length);

        vm.gridOptions.api.setRowData(list);
        
        vm.gridOptions.api.removeEventListener('rowClicked', onRowClicked);
        vm.gridOptions.api.addEventListener('rowClicked', onRowClicked);
      });

      function onRowClicked(e) {
          ItemDetailsService.openModal(e.data.id);
        }
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('app')
    .factory('UtilsService', UtilsService);

  /** @ngInject */
  function UtilsService($filter) {

    var Service = {
      formatDate: formatDate
    };

    function formatDate(date) {
      return $filter('date')(date, 'dd/MM/yyyy');
    }

    return Service;
  }
})();

(function() {
  'use strict';

  angular
    .module('app')
    .factory('ItemDetailsService', ItemDetailsService);

  /** @ngInject */
  function ItemDetailsService($uibModal, $log, GeoDataService, FilterService) {

    var Service = {
      openModal: openModal
    };


    function openModal(id) {
      var modalInstance = $uibModal.open({
        animation: true,
        templateUrl: 'app/components/itemDetailsModal/itemDetailsModal.tpl.html',
        controller: 'ItemDetailsModalController',
        controllerAs: 'vm',
        bindToController: true,
        size: 'lg',
        resolve: {
          item: GeoDataService.getItem(id)
        }
      });

      modalInstance.result.then(function (item) {
        FilterService.isolateItemAndRefresh(item);
      }, function () {
        $log.info('ItemDetailsModal dismissed at: ' + new Date());
      });
    }

    return Service;
  }
})();

(function() {
  'use strict';

  angular
    .module('app')
    .factory('GridService', GridService);

  /** @ngInject */
  function GridService() {

    var columnDefs = [
      {headerName: "Nom", field: "name"},
      {headerName: "Date", field: "registered", width: 110, comparator: dateComparator},
      {headerName: "Genre", field: "gender", width: 90, cellRenderer: cellRendererGender},
      {headerName: "Téléphone", field: "phone"},
      {headerName: "Lat", field: "latitude", width: 90},
      {headerName: "Lon", field: "longitude", width: 90},
      {headerName: "Id", field: "id"}
    ];

    var gridOptions = {
      columnDefs: columnDefs,
      enableSorting: true,
      pinnedColumnCount: 1,
      rowData: []
    };

    var Service = {
      gridOptions: gridOptions,
      dateComparator: dateComparator
    };

    function dateComparator(date1, date2) {
      var date1Number = monthToComparableNumber(date1);
      var date2Number = monthToComparableNumber(date2);

      if (date1Number===null && date2Number===null) {
        return 0;
      }
      if (date1Number===null) {
        return -1;
      }
      if (date2Number===null) {
        return 1;
      }

      return date1Number - date2Number;
    }

    function monthToComparableNumber(date) {
      if (date === undefined || date === null || date.length !== 10) {
          return null;
      }

      var yearNumber = date.substring(6,10);
      var monthNumber = date.substring(3,5);
      var dayNumber = date.substring(0,2);

      var result = (yearNumber*10000) + (monthNumber*100) + dayNumber;
      return result;
    }

    function cellRendererGender(params) {
      if (params.value == 'female') {
        return 'Femme';
      } else {
        return 'Homme';
      }
    }

    return Service;
  }
})();

(function() {
  'use strict';

  angular
    .module('app')
    .factory('GeoDataService', GeoDataService);

  /** @ngInject */
  function GeoDataService($location, $log, $http, $timeout, $q, GridService, UtilsService, Files) {
    var _list = [];
    var _filteredList = [];
    var _observer;

    L.Icon.Default.imagePath = 'assets/images/leaflet';

    var Service = {
      totalItems: 0,
      mapWIP: true,
      loadData: loadData,
      filterData: filterData,
      getItem: getItem,
      list: Rx.Observable.create(function(observer) {
        _observer = observer;
      }).share()
    };

    $timeout(function() {
      _observer.onNext(_list);
    });

    function loadData() {
      var _files = Files.tab102k;

      if($location.search()) {
        if($location.search().tab == '32k') _files = Files.tab32k;
        if($location.search().tab == '64k') _files = Files.tab64k;
      }

      $q.all(_files.map(function (item) {
        return $http({
          method: 'GET',
          url: item
        });
      })).then(loadDataComplete).catch(loadDataFailed);

      function loadDataComplete(results) {
        angular.forEach(results, function (result) {
          _list = _list.concat(result.data);
        });

        
        $timeout(function() {
          Service.totalItems = _list.length;
          _observer.onNext(_list);
        }, 1000);
      }

      function loadDataFailed(error) {
        $log.error('XHR Failed for getData.' + angular.toJson(error.data, true));
      }
    }

    function getItem(id) {
      return _.find(_list, function(item) { return item.id == id; });
    }

    function filterData(filter) {

      $timeout(function() {

        // Si aucun filtre activé, on retourne la liste compléte
        if( !filter.name.enabled && !filter.gender.enabled && !filter.position.enabled && !filter.date.enabled )
        {
          _observer.onNext(_list);
          return;
        }

        _filteredList = _list.filter(function(item) {

          // Filter on NAME
          if( filter.name.enabled && 
              filter.name.value != '' && 
              item.name.indexOf(filter.name.value) == -1 )
                return false;

          // Filter on GENDER
          if( filter.gender.enabled && 
              filter.gender.value != '' && 
              item.gender != filter.gender.value ) 
                return false;

          // Filter on POSITION
          if(filter.position.enabled) {
            if( filter.position.startLatValue != '' && 
                item.latitude < filter.position.startLatValue ) 
                  return false;

            if( filter.position.endLatValue != '' && 
                item.latitude > filter.position.endLatValue ) 
                  return false;

            if( filter.position.startLngValue != '' && 
                item.longitude < filter.position.startLngValue ) 
                  return false;

            if( filter.position.endLngValue != '' && 
                item.longitude > filter.position.endLngValue ) 
                  return false;
          }

          // Filter on DATE
          if(filter.date.enabled) {
            if( filter.date.startValue != '' && 
                GridService.dateComparator(UtilsService.formatDate(filter.date.startValue), item.registered) > 0 ) 
                  return false;

            if( filter.date.endValue != '' && 
                GridService.dateComparator(UtilsService.formatDate(filter.date.endValue), item.registered) < 0 ) 
                  return false;
          }
          return true;
        });

        _observer.onNext(_filteredList);
      });
    }

    return Service;
  }
})();

(function() {
  'use strict';

  angular
    .module('app')
    .factory('FilterService', FilterService);

  /** @ngInject */
  function FilterService($uibModal, $log, $sce, GeoDataService, UtilsService, LocalisationPrecision) {
    var _currentFilter = {
      name: {
        enabled: false,
        value: ''
      },
      date: {
        enabled: false,
        startValue: '',
        endValue: ''
      },
      gender: {
        enabled: false,
        value: ''
      },
      position: {
        enabled: false,
        startLatValue: '',
        endLatValue: '',
        startLngValue: '',
        endLngValue: ''
      }
    };

    var datepickerOptions = {
      formatYear: 'yy',
      startingDay: 1
    };

    var Service = {
      filter: _currentFilter,
      datepickerOptions: datepickerOptions,
      filterDisplayText: getDisplay(),
      openModal: openModal,
      removeAll: removeAll,
      removeAllAndRefresh: removeAllAndRefresh,
      isolateItemAndRefresh: isolateItemAndRefresh
    };


    function openModal() {
      var modalInstance = $uibModal.open({
        animation: true,
        templateUrl: 'app/components/filterModal/erFilterModal.tpl.html',
        controller: 'ErFilterModalController',
        controllerAs: 'vm',
        bindToController: true,
        size: 'lg'
      });

      modalInstance.result.then(function () {
        refreshData();
      }, function () {
        $log.info('erFilterModal dismissed at: ' + new Date());
      });
    }

    function removeAll() {
      _currentFilter.name.enabled = false;
      _currentFilter.date.enabled = false;
      _currentFilter.gender.enabled = false;
      _currentFilter.position.enabled = false;
    }

    function removeAllAndRefresh() {
        removeAll();
        refreshData();
    }

    function isolateItemAndRefresh(item) {
        _currentFilter.name.enabled = false;

        _currentFilter.date.enabled = true;
        var _date = new Date( item.registered.split('/')[2], item.registered.split('/')[1]-1, item.registered.split('/')[0] );
        _currentFilter.date.startValue = _date;
        _currentFilter.date.endValue = _date;

        _currentFilter.gender.enabled = false;

        _currentFilter.position.enabled = true;
        _currentFilter.position.startLatValue = Math.floor((item.latitude - LocalisationPrecision.lat)*10000)/10000;
        _currentFilter.position.endLatValue = Math.floor((item.latitude + LocalisationPrecision.lat)*10000)/10000;
        _currentFilter.position.startLngValue = Math.floor((item.longitude - LocalisationPrecision.lng)*10000)/10000;
        _currentFilter.position.endLngValue = Math.floor((item.longitude + LocalisationPrecision.lng)*10000)/10000;

        refreshData();
    }

    function refreshData() {
      Service.filterDisplayText = getDisplay();
      GeoDataService.filterData(_currentFilter);
    }

    function getDisplay() {
      var txt = '';

      if(_currentFilter.name.enabled && _currentFilter.name.value != '') {
        txt += '<span>Nom: <span class="val">' + _currentFilter.name.value + '</span></span>';
      }
      if(_currentFilter.gender.enabled && _currentFilter.gender.value != '') {
        txt += '<span>Genre: <span class="val">' + (_currentFilter.gender.value == 'male' ? 'Homme' : 'Femme') + '</span></span>';
      }
      if(_currentFilter.date.enabled) {
        if(_currentFilter.date.startValue != '') txt += '<span>Date début: <span class="val">' + UtilsService.formatDate(_currentFilter.date.startValue) + '</span></span>';
        if(_currentFilter.date.endValue != '') txt += '<span>Date fin: <span class="val">' + UtilsService.formatDate(_currentFilter.date.endValue) + '</span></span>';
      }
      if(_currentFilter.position.enabled) {
        if(_currentFilter.position.startLatValue && _currentFilter.position.startLatValue != '') txt += '<span>Lat min: <span class="val">' + _currentFilter.position.startLatValue + '</span></span>';
        if(_currentFilter.position.endLatValue && _currentFilter.position.endLatValue != '') txt += '<span>Lat max: <span class="val">' + _currentFilter.position.endLatValue + '</span></span>';
        if(_currentFilter.position.startLngValue && _currentFilter.position.startLngValue != '') txt += '<span>Lng min: <span class="val">' + _currentFilter.position.startLngValue + '</span></span>';
        if(_currentFilter.position.endLngValue && _currentFilter.position.endLngValue != '') txt += '<span>Lng max: <span class="val">' + _currentFilter.position.endLngValue + '</span></span>';
      }

      return (txt == '' ? ' <span class="val">Aucun filtre</span>' : $sce.trustAsHtml(txt));
    }

    return Service;
  }
})();

